//查询所有考试
<template>
  <div class="exam">
    <el-table :data="pagination.records" border>
      <el-table-column fixed="left" prop="source" label="试卷名称" width="180"></el-table-column>
      <el-table-column prop="description" label="介绍" width="200"></el-table-column>
      <el-table-column prop="institute" label="所属学院" width="120"></el-table-column>
      <el-table-column prop="major" label="所属专业" width="200"></el-table-column>
      <el-table-column prop="grade" label="年级" width="100"></el-table-column>
      <el-table-column prop="examDate" label="考试日期" width="120"></el-table-column>
      <el-table-column prop="totalTime" label="持续时间" width="120"></el-table-column>
      <el-table-column prop="totalScore" label="总分" width="120"></el-table-column>
      <el-table-column prop="type" label="试卷类型" width="120"></el-table-column>
      <el-table-column prop="tips" label="考生提示" width="400"></el-table-column>
      <el-table-column fixed="right" label="操作" width="250">
        <template slot-scope="scope">
          <el-button @click="preShow(scope.row.examCode)" type="success" size="small">预览</el-button>
          <el-button @click="edit(scope.row.examCode)" type="primary" size="small">编辑</el-button>
          <el-button @click="deleteRecord(scope.row.examCode)" type="danger" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagination.current"
      :page-sizes="[4, 8, 10, 20]"
      :page-size="pagination.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total" class="page">
    </el-pagination>
    <!-- 预览整卷 -->
    <el-dialog
      title="预览整卷"
      :visible.sync="dialogVisibleNew"
      width="70%"
      lock-scroll="true"
      :before-close="handleClose" style="height:100%">
      <el-button type="primary" style="margin-left: 960px;" @click="downloadDocx(examData.examCode)" :loading="loadingDocx" icon="el-icon-download" >下载文档</el-button>
      <div>
        <div style="font-size: 20px;text-align: center;margin-bottom: 10px;">{{ examData.source }} 
        </div>
        <div v-if="score.length==4" style="text-align: center;margin-bottom: 10px;">（总分：{{ score[0]+score[1]+score[2]+score[3] }}分）</div>
        <el-collapse class="inner" v-model="activeNames"  style="height: 300px; overflow-y: auto;">
            <el-collapse-item v-if="topicCount[0]!=0">
              <template slot="title" name="1" >
                <div class="titlei" >一、选择题 (共{{topicCount[0]}}题 共计{{score[0]}}分)</div>
              </template>
              <div class="contenti">
                <ul class="question" style="margin-top: 10px;" v-for="(list, index) in topic[1]" :key="index">
                  <li>{{index+1}}. {{list.question}} ({{list.score}}分)</li>
                  <li>A.{{list.answerA  }}</li><li>B.{{list.answerB  }}</li><li>C.{{list.answerC  }}</li><li>D.{{list.answerD  }}</li>
                </ul>
              </div>
            </el-collapse-item>
            <el-collapse-item v-if="topicCount[1]!=0">
              <template slot="title" name="2" >
                <div class="titlei">二、填空题 (共{{topicCount[1]}}题 共计{{score[1]}}分)</div>
              </template>
              <div class="contenti">
                <ul class="question" style="margin-top: 10px;" v-for="(list, index) in topic[2]" :key="index">
                  <li>{{index+1}}. {{list.question}} ({{list.score}}分)</li>
                </ul>
              </div>
            </el-collapse-item>
            <el-collapse-item v-if="topicCount[2]!=0">
              <template slot="title" name="3" >
                <div class="titlei">三、判断题 (共{{topicCount[2]}}题 共计{{score[2]}}分)</div>
              </template>
              <div class="contenti">
                <ul class="question" style="margin-top: 10px;" v-for="(list, index) in topic[3]" :key="index">
                  <li>{{index+1}}. {{list.question}} ({{list.score}}分)</li>
                </ul>
              </div>
            </el-collapse-item>
            <el-collapse-item  v-if="topicCount[3]!=0">
              <template slot="title" name="4">
                <div class="titlei">四、应用分析题 (共{{topicCount[3]}}题 共计{{score[3]}}分)</div>
              </template>
              <div class="contenti">
                <ul class="question" style="margin-top: 10px;" v-for="(list, index) in topic[4]" :key="index">
                  <li>{{index+1}}. {{list.question}} ({{list.score}}分)</li>
                </ul>
              </div>
            </el-collapse-item>
        </el-collapse>
      </div>
      
    </el-dialog>

    <!-- 编辑对话框-->
    <el-dialog
      title="编辑试卷信息"
      :visible.sync="dialogVisible"
      width="70%"
     
      :before-close="handleClose">
      <section class="update">
        <el-form ref="form" :inline="true" :model="form" label-width="80px">
          <el-form-item label="试卷名称">
            <el-input v-model="form.source" style="width: 200px;"></el-input>
          </el-form-item>
          <el-form-item label="介绍">
            <el-input v-model="form.description" style="width: 300px;"></el-input>
          </el-form-item>
          <el-form-item label="所属学院">
            <el-input v-model="form.institute" style="width: 200px;"></el-input>
          </el-form-item>
          <el-form-item label="所属专业">
            <el-input v-model="form.major" style="width: 300px;"></el-input>
          </el-form-item>
          <el-form-item label="年级">
            <el-input v-model="form.grade" style="width: 200px;"></el-input>
          </el-form-item>
          <el-form-item label="考试日期">
            <el-col :span="11">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.examDate" style="width: 200px;"></el-date-picker>
            </el-col>
          </el-form-item>
          <el-form-item label="平时与期末占比">
            <el-select v-model="form.rate" placeholder="请选择" style="width: 100px;">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="持续时间" style="margin-left: 100px;">
            <el-input v-model="form.totalTime" maxlength="3" style="width: 60px;"></el-input>
          </el-form-item>
          <el-form-item label="总分" style="margin-left: 85px;">
            <el-input v-model="form.totalScore" maxlength="3" style="width: 60px;"></el-input>
          </el-form-item>
          <el-form-item label="试卷类型" style="margin-left: 105px;">
            <el-input v-model="form.type" style="width: 96px;"></el-input>
          </el-form-item>
          <el-form-item label="考生提示">
            <el-input type="textarea" v-model="form.tips" style="width: 890px;"></el-input>
          </el-form-item>
        </el-form>
      </section>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loadingDocx:false,
      activeNames: ['3'],
      topicCount: [],//每种类型题目的总数
      score: [],  //每种类型分数的总数
      topic: {  //试卷信息

      },
      examData: { //考试信息
        // source: null,
        // totalScore: null,
      },
      options: [{
          value: '1:9',
          label: '1:9'
        }, {
          value: '2:8',
          label: '2:8'
        }, {
          value: '3:7',
          label: '3:7'
        }, {
          value: '4:6',
          label: '4:6'
        }, {
          value: '5:5',
          label: '5:5'
        }],
        value: '',
      form: {}, //保存点击以后当前试卷的信息
      pagination: { //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 4 //每页条数
      },
      dialogVisible: false,
      dialogVisibleNew:false
    }
  },
  created() {
    this.getExamInfo()
  },
  methods: {
    downloadDocx(examCode){
      this.loadingDocx=true
      this.$axios({
        responseType:'blob',
        url:process.env.BASE_API_OTHER+'/exportDocx/'+examCode,
        method:'put'
      }).then(res=>{
        this.loadingDocx=false
        let blob = new Blob([res.data], { type: "application/docx" });
        let url = window.URL.createObjectURL(blob);
        const link = document.createElement("a"); // 创建a标签
        link.href = url;
        link.download = this.examData.source+".docx"; // 重命名文件
        link.click();
        URL.revokeObjectURL(url);

        this.$message({
          type:'success',
          message:'导出成功'
        })
      })
    },
    init(examCode) {
      this.topicCount= [],//每种类型题目的总数
      this.score= [],  //每种类型分数的总数
      this.$axios(`${process.env.BASE_API_EXAM}/exam/${examCode}`).then(res => {  //通过examCode请求试卷详细信息
        res.data.data.examDate = res.data.data.examDate.substr(0,10)
        this.examData = { ...res.data.data}
        let paperId = this.examData.paperId
        this.$axios(`${process.env.BASE_API_EXAM}/paper/${paperId}`).then(res => {  //通过paperId获取试题题目信息
          this.topic = {...res.data}
          let keys = Object.keys(this.topic) //对象转数组
          keys.forEach(e => {
            let data = this.topic[e]
            this.topicCount.push(data.length)
            let currentScore = 0
            for(let i = 0; i< data.length; i++) { //循环每种题型,计算出总分
              currentScore += data[i].score
            }
            this.score.push(currentScore) //把每种题型总分存入score
          })
          console.log(this.score+" "+this.topicCount)
        })
      })
    },
    edit(examCode) { //编辑试卷
      this.dialogVisible = true
      this.$axios(`${process.env.BASE_API_EXAM}/exam/${examCode}`).then(res => { //根据试卷id请求后台
        if(res.data.code == 200) {
          this.form = res.data.data
        }
      })
    },
    preShow(examCode) { //预览试卷
      this.dialogVisibleNew=true
      this.init(examCode)
      // this.dialogVisible = true
      // this.$axios(`${process.env.BASE_API_EXAM}/exam/${examCode}`).then(res => { //根据试卷id请求后台
      //   if(res.data.code == 200) {
      //     this.form = res.data.data
      //   }
      // })
    },
    handleClose(done) { //关闭提醒
      this.$confirm('确认关闭？')
        .then(_ => {
          this.loadingDocx=false
          done();
        }).catch(_ => {});
    },
    submit() { //提交修改后的试卷信息
      this.dialogVisible = false
      this.$axios({
        url: process.env.BASE_API_EXAM+'/exam',
        method: 'put',
        data: {
          ...this.form
        }
      }).then(res => {
        if(res.data.code == 200) {
          this.$message({ //成功修改提示
            message: '更新成功',
            type: 'success'
          })
        }
        this.getExamInfo()
      })
    },
    deleteRecord(examCode) {
      this.$confirm("确定删除该记录吗,该操作不可逆！！！","删除提示",{
        confirmButtonText: '确定删除',
        cancelButtonText: '算了,留着',
        type: 'danger'
      }).then(()=> { //确认删除
        this.$axios({
          url: `${process.env.BASE_API_EXAM}/exam/${examCode}`,
          method: 'delete',
        }).then(res => {
          this.getExamInfo()
        })
      }).catch(() => {

      })
    },
    getExamInfo() { //分页查询所有试卷信息
      this.$axios(`${process.env.BASE_API_EXAM}/exams/${this.pagination.current}/${this.pagination.size}`).then(res => {
        this.pagination = res.data.data
      }).catch(error => {
      })
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val
      this.getExamInfo()
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val
      this.getExamInfo()
    },
  },
};
</script>
<style lang="less" scoped>
/* 自定义滚动条样式 */
.exam {
  padding: 0px 40px;
  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .edit{
    margin-left: 20px;
  }
}
</style>



